.gifts {
  margin-top: 40px;

  @media (min-width: @tablet-width) {
    margin-top: 64px;
  }

  @media (min-width: @desktop-width) {
    width: 245px;
    margin-top: 0;
  }
}

.gifts__item {
  padding-bottom: 40px;

  background-color: @green;
  background-image: url("../img/backgrounds/gift.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/backgrounds/gift@2x.jpg");
  }

  box-sizing: border-box;
  position: relative;

  &::before {
    content: "";

    width: 80px;
    height: 80px;

    background-image: url("../img/icons/gift.svg");
    background-repeat: no-repeat;

    position: absolute;
    top: 58px;
    left: 50%;

    transform: translateX(-50%);
  }

  @media (min-width: @tablet-width) {
    width: 708px;
    min-height: 200px;

    margin: 0 auto;
    padding-top: 70px;
    padding-bottom: 70px;

    background-image: url("../img/backgrounds/gift-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/backgrounds/gift-tablet@2x.jpg");
    }

    &::before {
      top: 50%;
      left: 78px;

      transform: translate(0, -50%);
    }
  }

  @media (min-width: @desktop-width) {
    width: auto;
    padding: 0;
    padding-bottom: 40px;

    background-image: url("../img/backgrounds/gift.jpg");
    background-size: cover;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/backgrounds/gift@2x.jpg");
    }

    &::before {
      top: 58px;
      left: 50%;

      transform: translate(-50%, 0);
    }
  }
}

.gifts__title {
  color: @white;
  font-family: @sub-font;
  font-size: 16px;
  line-height: 1.25;

  text-align: center;

  width: 165px;
  margin: 0 auto;
  padding-top: 185px;

  @media (min-width: @tablet-width) {
    text-align: left;
    width: auto;

    margin-left: 420px;
    margin-right: 105px;
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    text-align: center;
    width: 165px;
    margin: 0 auto;
    padding-top: 185px;
  }
}
