.additional-goods {
  padding: 25px 20px 35px;
  overflow: hidden;

  .title {
    margin-top: 0;
    margin-bottom: 30px;
  }

  @media (min-width: @tablet-width) {
    padding: 70px 30px 75px;

    .title {
      margin-left: 37px;
      margin-bottom: 80px;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 75px;
    padding-bottom: 85px;

    .title {
      margin-left: 0;
      margin-bottom: 83px;
    }
  }
}

.additional-goods__box {
  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 895px 245px;
    grid-column-gap: 80px;
  }
}

.additional-goods__list {

  .reset-ul;

  @media (min-width: @desktop-width) {
    margin-top: 0;
  }
}

.additional-goods__item {
  padding-top: 12px;
  padding-bottom: 16px;

  border-top: 1px solid @border-color-secondary;

  &:last-child {
    border-bottom: 1px solid @border-color-secondary;
  }

  @media (min-width: @tablet-width) {
    padding: 28px 38px;

    border-right: 1px solid @border-color-secondary;
    border-left: 1px solid @border-color-secondary;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 610px 245px;
    align-items: center;
    grid-column-gap: 40px;

    padding: 15px 0 16px;
    border-left: none;
    border-right: none;
  }
}

.additional-goods__content {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 0;
  }
}

.additional-goods__title-item {
  color: @special-dark-grey;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;

  margin-top: 0;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    display: inline-block;

    font-size: 20px;
    margin-bottom: 0;
  }
}

.additional-goods__description {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  font-family: @sub-font;
  font-size: 14px;

  margin-bottom: 15px;

  @media (min-width: @tablet-width) {
    display: inline-flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    width: 285px;
    margin-top: 5px;
  }
}

.additional-goods__description-text {
  margin-left: 0;

  @media (min-width: @tablet-width) {
    margin-left: 50px;
  }
}
