@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.reset-ul {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}
.page {
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 18px;
  height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  color: #444444;
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  max-width: 1220px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container--header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 30px;
  }
}
@media (min-width: 1440px) {
  .container--header {
    padding-top: 52px;
  }
}
@media (min-width: 768px) {
  .container--questionnaire {
    padding: 0 70px;
  }
}
@media (min-width: 768px) {
  .container--catalog {
    padding: 0 30px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title {
  display: inline-block;
  color: #000000;
  font-size: 24px;
  line-height: 1.1;
  font-weight: 400;
  background-color: #ffffff;
}
.title--index {
  font-size: 36px;
  background-color: inherit;
}
@media (min-width: 768px) {
  .title {
    font-size: 36px;
    line-height: 1;
  }
  .title--index {
    font-size: 60px;
    line-height: 1;
  }
}
.title__line {
  position: relative;
  padding-right: 23px;
}
.title__line::before,
.title__line::after {
  content: "";
  width: 100vw;
  height: 2px;
  background-color: #68B738;
  position: absolute;
  top: 20px;
  left: 0;
  z-index: -1;
}
@media (min-width: 768px) {
  .title__line {
    display: inline-block;
    margin-left: -40px;
    padding-left: 40px;
    padding-right: 40px;
    background-color: #ffffff;
  }
  .title__line::before,
  .title__line::after {
    top: 24px;
  }
  .title__line::before {
    left: -100vw;
  }
}
.button {
  display: block;
  color: #ffffff;
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  padding: 11px;
  background-color: #68B738;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 2, 0.3);
}
.button--form {
  line-height: 1.25;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .button--form {
    font-size: 20px;
    margin: 0;
    padding: 13px;
  }
}
@media (min-width: 1440px) {
  .button--form {
    width: 570px;
  }
}
.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button--secondary:hover,
.button--secondary:focus {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}
.header {
  padding-top: 12px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    padding-top: 0;
  }
}
.logo {
  transition: 0.2s;
}
.logo:hover,
.logo:focus {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
.logo--header {
  padding-left: 20px;
}
@media (min-width: 768px) {
  .logo--header {
    padding-left: 0;
  }
  .logo--header img {
    width: 174px;
  }
}
@media (min-width: 1440px) {
  .logo--header img {
    width: 202px;
  }
}
.logo--footer {
  text-align: center;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .logo--footer {
    margin-bottom: 0;
  }
}
.nav {
  margin-top: 13px;
  position: relative;
}
@media (min-width: 768px) {
  .nav {
    margin-top: 5px;
  }
}
@media (min-width: 1440px) {
  .nav {
    z-index: 2;
  }
}
.nav--opened .nav__list {
  display: block;
}
.nav--closed {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav--closed {
    border-bottom: none;
  }
}
.nav--closed .nav__list {
  display: none;
}
@media (min-width: 768px) {
  .nav--closed .nav__list {
    display: flex;
  }
}
.nav--opened .nav__toggle-item {
  height: 0;
}
.nav--opened .nav__toggle-item::before {
  transform: rotate(45deg);
}
.nav--opened .nav__toggle-item::after {
  transform: rotate(-45deg);
}
.nav--nojs .nav__toggle {
  display: none;
}
.nav--nojs .nav__list {
  display: block;
}
.nav__toggle {
  display: block;
  width: 24px;
  height: 18px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: -43px;
  right: 20px;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle-item,
.nav__toggle-item::before,
.nav__toggle-item::after {
  width: 24px;
  height: 2px;
  margin-top: -1px;
  margin-left: -12px;
  background-color: #000000;
  position: absolute;
  top: 50%;
  left: 50%;
}
.nav__toggle-item::before,
.nav__toggle-item::after {
  display: block;
  content: "";
  transition: 0.2s;
}
.nav__toggle-item::before {
  transform: translateY(-8px);
}
.nav__toggle-item::after {
  transform: translateY(8px);
}
.nav__list {
  display: flex;
  flex-direction: column;
  text-align: center;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
  }
}
@media (min-width: 1440px) {
  .nav__list--index .nav__link {
    color: #ffffff;
  }
  .nav__list--index .nav__link--active {
    border-color: #ffffff;
  }
}
.nav__item {
  padding: 20px 20px 23px;
  border-bottom: 1px solid #e6e6e6;
}
.nav__item:first-child {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    margin-right: 35px;
    padding: 0;
    border: none;
  }
  .nav__item:first-child {
    border-top: none;
  }
  .nav__item:last-child {
    margin-right: 0;
  }
}
.nav__link {
  color: #000000;
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 20px;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.2s;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 0.6;
}
.nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__link {
    font-size: 18px;
    line-height: 1.3;
    font-weight: 400;
  }
  .nav__link--active {
    padding-bottom: 7px;
    border-bottom: 2px solid #68B738;
  }
}
@media (min-width: 1440px) {
  .nav__link {
    font-size: 20px;
  }
}
.about {
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/cat.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  z-index: 1;
}
@media (min-resolution: 2dppx) {
  .about {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/cat@2x.jpg");
  }
}
@media (min-width: 768px) {
  .about {
    background: #ffffff;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .about {
    padding-left: 0;
    padding-right: 0;
  }
  .about::after {
    content: "";
    width: 50%;
    height: 694px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/cat-desktop.jpg");
    background-repeat: no-repeat;
    position: absolute;
    top: -138px;
    right: 0;
    z-index: -2;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
