    grid-template-columns: repeat(3, 324px);
    width: 1092px;
  }
}
@media (min-width: 1440px) {
  .catalog__list {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 147px 80px;
    width: auto;
  }
}
.catalog__item {
  margin-bottom: 25px;
  padding: 20px 20px 0;
  border-top: 1px solid #ebebeb;
}
.catalog__item--see-more {
  text-align: center;
  margin-bottom: 0;
  padding-top: 75px;
  padding-bottom: 22px;
  border-bottom: 1px solid #ebebeb;
  position: relative;
}
.catalog__item--see-more .catalog__text {
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 12px;
  line-height: 1.3;
  margin-top: 0;
}
.catalog__item--see-more .button {
  margin-top: 23px;
}
.catalog__item--see-more::before,
.catalog__item--see-more::after {
  content: "";
  background-color: #d3d3d3;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.catalog__item--see-more::before {
  width: 30px;
  height: 2px;
  top: 35px;
}
.catalog__item--see-more::after {
  width: 2px;
  height: 30px;
  top: 20px;
}
@media (min-width: 768px) {
  .catalog__item {
    margin-bottom: 0;
    padding: 156px 36px 40px;
    background-color: #f2f2f2;
    border: none;
    position: relative;
  }
  .catalog__item--see-more {
    padding-bottom: 80px;
    background-color: #ffffff;
    border: 2px solid #f2f2f2;
  }
  .catalog__item--see-more .catalog__text {
    font-size: 16px;
    line-height: 1.125;
    width: 170px;
    margin-left: auto;
    margin-right: auto;
  }
  .catalog__item--see-more .button {
    width: 250px;
    margin-top: 0;
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    bottom: 35px;
    transform: translateX(-50%);
  }
  .catalog__item--see-more::before {
    width: 60px;
    top: 80px;
  }
  .catalog__item--see-more::after {
    height: 60px;
    top: 50px;
  }
}
@media (min-width: 1440px) {
  .catalog__item {
    padding-top: 160px;
  }
  .catalog__item--see-more .button {
    width: 170px;
  }
}
